body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Map {
    position: relative;
}

.Map-header {
    z-index: 1000;
    position: fixed;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    width: 200px;
    margin-left: 5px;
}

.Map-name {
    font-size: 24px;
    line-height: 30px;
    color: white;
}

.Map-search {
    display: inline-block;
}

.Map-search-label {
    display: inline-block;
    font-size: 16px;
    line-height: 30px;
    text-align: left;
    color: white;
}

.Map-search-input {
    display: inline-block;
    font-size: 16px;
    width: 100px;
    margin-left: 5px;
}

.Map-log-button {
    margin-top: 5px;
    padding: 5px;
    background-color: black;
    border-color: white;
    border-style: solid;
    color: white;
    border-width: 1px;
    border-radius: 5px;
    cursor: default;
}

.Map-search-match-button {
    margin-top: 5px;
    padding: 5px;
    border-color: white;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    cursor: default;
    color: white;
}
.Region {
    position: absolute;
    cursor: default;
    border-style: solid;
    border-radius: 5px;
    border-width: 1px;
    border-color: lightgray;
    color: white;
    padding: 5px;
    font-size: 8px;
    font-weight: bold;
}

.Region.Region-matching {
    border-width: 2px;
    border-color: dodgerblue;
    color: deepskyblue;
}

.Region.Region-selected {
    border-width: 2px;
    border-color: teal;
    color: mediumturquoise;
}
.SearchResults {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    align-content: space-between;
    border-color: lightgray;
    border-style: solid;
    border-width: 1px;
    background-color: dimgrey;
    color: lightgray;
    cursor: default;
}

.SearchResults-selected {
    font-size: 10px;
    width: 138px;
}

.SearchResults-selected:hover {
    color: white;
}

.SearchResults-next-button,.SearchResults-previous-button {
    font-size: 10px;
    width: 10px;
    padding: 2px;
    line-height: 10px;
}

.SearchResults-next-button:hover,.SearchResults-previous-button:hover {
    color: white;
}
